import React from 'react';
import './Contact.css';
import { connect } from 'react-redux';

function Contact(props) {
    const { contacts } = props;

    return (
        <div className="contactPage">
            <div className="pageTitle">
                Contact Me!
            </div>
            <div className="contactInfo">
                {
                    contacts.map((contactObj) => (
                        <div className="emailContainer row" key={contactObj.id}>
                            <div className="col-sm-6 col-md-6 col-xs-12">
                                <div className="contactTitle">
                                    <p>{contactObj.type === 'mail' ? 'Email' : 'Phone'}</p>
                                </div>
                            </div>
                            <div className="col-sm-6 col-md-6 col-xs-12">
                                <div className="contactLink">
                                    <a href={`${contactObj.type === 'mail' ? 'mailto:' : 'callto:'}${contactObj.value}`} className="link">{contactObj.value}</a>
                                </div>
                            </div>
                        </div>
                    ))
                }
            </div>
        </div>
    )
}
const mapStateToProps = (state) => ({
    contacts: state.contacts
})
export default connect(mapStateToProps)(Contact)