import React from 'react';
import Accordion from '../../components/accordion/Accordion';
import { connect } from 'react-redux';

function renderAccordionContent(expObj) {
    return (
        <table className="table">
            <tbody>
                <tr>
                    <td className='tableTitle'>From</td>
                    <td>{expObj.start_month + ' ' + expObj.start_year}</td>
                </tr>
                <tr>
                    <td className='tableTitle'>Designation</td>
                    <td>{expObj.designation}</td>
                </tr>
                <tr>
                    <td className='tableTitle'>Location</td>
                    <td>{expObj.location}</td>
                </tr>
                <tr>
                    <td className='tableTitle'>Current Employer</td>
                    <td>{expObj.still_working ? 'Yes' : 'No'}</td>
                </tr>
                {
                    !expObj.still_working && (<tr>
                        <td className='tableTitle'>To</td>
                        <td>{expObj.end_month + ' ' + expObj.end_year}</td>
                    </tr>)
                }
            </tbody>
        </table>
    )
}

function Experiance(props) {
    const { experiances: experianceArr } = props;
    return (
        <div className="experiancePage">
            <div className="pageTitle">
                <p>Experiance</p>
            </div>
            {
                experianceArr.map((expObj) =>
                    <Accordion key={expObj.id} company_name={expObj.company_name} >
                        {
                            renderAccordionContent(expObj)
                        }
                    </Accordion>
                )
            }
        </div>
    )
}
const mapStateToProps = (state) => ({
    experiances: state.experiances
})

export default connect(mapStateToProps)(Experiance);